<script>
import numberFormat from '@/helpers/numberFormat'

export default {
  filters: {
    numberFormat
  },
  computed: {
    orders() {
      return this.$store.state.orderInfo.basket ? this.$store.state.orderInfo.basket.items : []
    }
  },
  created() {
    if(this.$store.state.order && this.$store.state.orderInfo.id === this.$store.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id)
  }
}
</script>
