<template>
   <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        3 
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <BasedFormText v-model="formData.name" :error="formError.name" title="" placeholder="   "/>
            <BasedFormText v-model="formData.address" :error="formError.address" title=" " placeholder="  "/>
            <BasedFormText v-model="formData.phone" :error="formError.phone" title="" placeholder="  "/>
            <BasedFormText v-model="formData.email" :error="formError.email" title="Email" placeholder="  Email"/>
            <BasedFormTextarea v-model="formData.comment" :error="formError.comment" title="  " placeholder=" "/>

          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                     <b>500 </b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>: <b>3</b>    {{ cartPrice | numberFormat }}<b></b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
             
          </button>
          <div v-show="orderSending"> , </div>
          <div v-show="orderNotSent">  ,  </div>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'
import BasedFormField from '@/components/BasedFormField.vue'
import BasedFormText from '@/components/BasedFormText.vue'
import BasedFormTextarea from '@/components/BasedFormTextarea.vue'
import OrderItem from '@/components/OrderItem'
import axios from 'axios'
import { mapGetters } from 'vuex'
import { API_BASE_URL } from '../config'

export default {
  filters: {
    numberFormat
  },
  components: { BasedFormText, BasedFormTextarea, OrderItem },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderSending: false,
      orderNotSent: false
    }
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderSending = true,
      this.orderNotSent = false,

      axios.post(API_BASE_URL + '/api/orders', {
        ...this.formData
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey
        }
      })
      .then(response => {
        this.$store.commit('resetCart')
        this.$store.commit('updateOrderInfo', response.data)
        this.$router.push({name: 'orderInfo', params: {id: response.data.id}})
      })
      .catch(error => {
        this.formError = error.response.data.error.request || {};
        this.formErrorMessage = error.response.data.error.message;
        this.orderSending = false;
        this.orderNotSent = true;
      })
      .then(() => this.orderNotSent = true)
    }
  },
  computed: {
    ...mapGetters ({products: 'cartDetailProducts', cartPrice: 'cartTotalPrice'})
  }
}
</script>
